import React, { Component } from 'react'
import { init } from 'ityped'

export default class Hello extends Component {
  componentDidMount(){
    const myElement = document.querySelector('#myElement')
    init(myElement, { showCursor: false, strings: ['Hello World!', 'Use with React.js', 'tropusproject@2021' ] , typeSpeed:170, backDelay:1500 })
  }
  render(){
    return <p style={{ fontFamily: 'monospace', fontSize:'4vw' }} id="myElement"></p>
  }
}